/* Favorites page styles */
.empty-page {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 200px;
  max-width: 300px;
  color: var(--text-color);
  font-size: 14px;
  gap: 24px;
  margin-bottom: 60px;
}
.favorites-img-tablet,
.favorites-img-desktop {
  display: none;
}

.recipe-card {
  max-width: 258px;
  max-height: 287px;
}
.details-card-container {
  bottom: 16px;
  width: 80%;
}
.recipe-results {
  padding: 16px;
  gap: 60px;
}

.rating {
  display: flex;
  gap: 2px;
  align-items: center;
  color: #fff;
}
.stars-container {
  display: flex;
}

/* pagination*/
.pagination-controls-fav {
  display: flex;
  justify-content: center;
  gap: 14px;
  margin-top: 70px;
  display: flex;
  align-items: center;
  max-width: 600px;
}
.recipe-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.main-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/* Tablet */
@media screen and (min-width: 768px) {
  /* Favorites page styles */

  .empty-page {
    font-size: 16px;
    margin-top: 20px;
    max-width: 400px;
  }
  .favorites-img-tablet {
    display: block;
  }
  .recipe-card {
    max-width: 215px;
  }
  .recipe-results {
    max-width: 1100px;
    gap: 10px;
  }
  .rating {
    flex-direction: row;
  }
  .recipe-card-button {
    width: 61px;
    margin-left: 9px;
    font-size: 11px;
  }
  .stars-container {
    display: flex;
    width: 45px;
  }
}

/* Desktop */
@media screen and (min-width: 1280px) {
  /* Favorites page styles */
  .empty-page {
    max-width: 524px;
  }
  .favorites-img-desktop {
    display: block;
  }
  .favorites-img-tablet {
    display: none;
  }
  /* .recipe-card {
    max-width: 258px;
  }
  .recipe-card-button {
    width: 89px;
    height: 31px;
    font-size: 12px;
    margin-left: 38px;
  } */
  .stars-container {
    display: flex;
    width: 75px;
  }
}
